.ListHeader {
  padding: 0 100px;
}

.ListHeader-row {
  background-color: #f8f8f8;
  display: flex;
  align-items: center;

  border-radius: 10px;
}

.ListHeader-item {
  padding: 15px;
  color: rgb(255 188 0);
}

.ListHeader-item:nth-child(2) {
  width: 23%;
}
.ListHeader-item:nth-child(3) {
  width: 30%;
}
.ListHeader-item:nth-child(4) {
  width: 17%;
}
.ListHeader-item:nth-child(5) {
  width: 15%;
}
.ListHeader-item:nth-child(6) {
  width: 15%;
}
